import React, { useState } from 'react'

import Wrapper from './components/Wrapper'
import Screen from './components/Screen'
import ButtonBox from './components/ButtonBox'
import Button from './components/Button'

const btnValues = [
  ['C', '+-', '%', '/'],
  [7, 8, 9, 'X'],
  [4, 5, 6, '-'],
  [1, 2, 3, '+'],
  [0, '.', '='],
]

// num, the entered value;
// sign, the selected sign: and
// res, the calculated value.

const toLocaleString = (num) =>
  String(num).replace(/(?<!\..*)(\d)(?=(?:\d{3})+(?:\.|$))/g, '$1 ')

const removeSpaces = (num) => num.toString().replace(/\s/g, '')

const App = () => {
  let [calc, setCalc] = useState({
    sign: '',
    num: 0,
    res: 0,
  })

  /*
The numClickHandler function gets triggered only if any of the number buttons (09) are pressed.
Then it gets the value of the Button and adds that to the current num value.

no whole numbers start with zero
there are no multiple zeros before the comma
the format will be 0. if . is pressed first
numbers are entered up to 16 integers long'

  */
  const numClickHandler = (e) => {
    e.preventDefault()
    const value = e.target.innerHTML

    if (removeSpaces(calc.num).length < 16) {
      setCalc({
        ...calc,
        num:
          calc.num === 0 && value === '0'
            ? '0'
            : removeSpaces(calc.num) % 1 === 0
            ? toLocaleString(Number(removeSpaces(calc.num + value)))
            : toLocaleString(calc.num + value),
        res: !calc.sign ? 0 : calc.res,
      })
    }
  }

  /*
The commaClickHandler function gets fired only if the decimal point (.) is pressed.
It adds the decimal point to the current num value, making it a decimal number.
  */
  const commaClickHandler = (e) => {
    e.preventDefault()
    const value = e.target.innerHTML

    setCalc({
      ...calc,
      num: !calc.num.toString().includes('.') ? calc.num + value : calc.num,
    })
  }

  /*
The signClickHandler function gets fired when the user press either +, , * or /.
The particular value is then set as a current sign value in the calc object.
It will also make sure that theres no effect on repeated calls:
  */
  const signClickHandler = (e) => {
    e.preventDefault()
    const value = e.target.innerHTML

    setCalc({
      ...calc,
      sign: value,
      res: !calc.res && calc.num ? calc.num : calc.res,
      num: 0,
    })
  }

  /*
The equalsClickHandler function calculates the result when the equals button (=) is pressed.
The calculation is based on the current num and res value, as well as the sign selected

It will also make sure that:
theres no effect on repeated calls
users cant divide with 0
*/
  const equalsClickHandler = () => {
    if (calc.sign && calc.num) {
      const math = (a, b, sign) =>
        sign === '+'
          ? eval(a + b)
          : sign === '-'
          ? eval(a - b)
          : sign === 'X'
          ? eval(a * b)
          : eval(a / b)

      setCalc({
        ...calc,
        res:
          calc.num === '0' && calc.sign === '/'
            ? "Can't divide with 0"
            : toLocaleString(
                math(
                  Number(removeSpaces(calc.res)),
                  Number(removeSpaces(calc.num)),
                  calc.sign,
                ),
              ),
        sign: '',
        num: 0,
      })
    }
  }

  /*
The invertClickHandler function first checks if theres any entered value (num)
or calculated value (res) and then inverts them by multiplying with -1
  */
  const invertClickHandler = () => {
    setCalc({
      ...calc,
      num: calc.num ? toLocaleString(removeSpaces(calc.num) * -1) : 0,
      res: calc.res ? toLocaleString(removeSpaces(calc.res) * -1) : 0,
      sign: '',
    })
  }

  /*
The percentClickHandler function checks if theres any entered value (num) 
or calculated value (res) and then calculates the percentage using the 
built-in Math.pow function, which returns the base to the exponent power:
*/
  const percentClickHandler = () => {
    let num = calc.num ? parseFloat(removeSpaces(calc.num)) : 0
    let res = calc.res ? parseFloat(removeSpaces(calc.res)) : 0

    setCalc({
      ...calc,
      num: (num /= Math.pow(100, 1)),
      res: (res /= Math.pow(100, 1)),
      sign: '',
    })
  }

  /*
The resetClickHandler function defaults all the initial values of calc, 
returning the calc state as it was when the Calculator app was first rendered:
*/
  const resetClickHandler = () => {
    setCalc({
      ...calc,
      sign: '',
      num: 0,
      res: 0,
    })
  }

  // The flat() method creates a new array with all sub-array elements
  // concatenated into it recursively up to the specified depth.
  return (
    <Wrapper>
      <Screen value={calc.num ? calc.num : calc.res} />
      <ButtonBox>
        {btnValues.flat().map((btn, i) => {
          return (
            <Button
              key={i}
              className={btn === '=' ? 'equals' : ''}
              value={btn}
              onClick={
                btn === 'C'
                  ? resetClickHandler
                  : btn === '+-'
                  ? invertClickHandler
                  : btn === '%'
                  ? percentClickHandler
                  : btn === '='
                  ? equalsClickHandler
                  : btn === '/' || btn === 'X' || btn === '-' || btn === '+'
                  ? signClickHandler
                  : btn === '.'
                  ? commaClickHandler
                  : numClickHandler
              }
            />
          )
        })}
      </ButtonBox>
    </Wrapper>
  )
}

export default App
